@import "common/mixins";
@import "common/variables";
@import "common/fonts";  
 
// =======================================================
//                  block-location                           
// =======================================================
.location{ 
  &__body{
     position: relative;
     @include jucc();
     width: 100%;
     height: 100%;
     padding: 3% 0% 0% 0%;  
  }
//  --------ширина шапки--------
  &__head{
    position: relative;
    flex: 0 1 100%;  
    padding: 0% 0% 0% 0%;
  }
// ----------ширина контента-----
  &__content {  
position: relative;
flex: 0 1 100%;
padding: 6% 0% 0% 0%; 
}
  &__content-low {  
flex: 0 1 100%;
padding: 4% 0% 0% 0%;  
}

&_about{
& .location__body{
  padding: 4.8% 0% 0% 0%;  
}
}

  }

// ----------------шапка блока---------------------
.location-head{
 width: 100%;
  height: 100%;
  &__body {
    position: relative;
    @include jucs();  
    text-align: left;
    width: 100%;
    height: 100%;
    flex: 0 1 100%;
    padding: 0% 0% 0% 0%;
  }

  &__title {
    position: relative;
    z-index: 2;
    @include Text-Style-18;   
    line-height: 0.8;  
    flex: 0 1 100%;
    padding: 0% 0% 0% 0%; 
   @media screen and (max-width: 678px) {   
     font-size: 30px;
     padding: 3% 0% 0% 0%;  
    text-align: center; }  
  

  }
}
// ---------управление единичными блоками---------------------
.content-location{
  &__body {  
position: relative;
@include jucb(); 
@include alis(); 

@media screen and (max-width: 1440px) {
padding: 0% 0% 0% 0%;
 
}
      @media screen and (max-width: 1080px) {
       padding: 0% 5% 0% 5%; 
      }
}
// ----ширина единичного блока-----
&__item {  
flex: 0 1 25%; 
@media screen and (max-width: 1220px) {
 flex: 0 1 30%; 
} 
@media screen and (max-width: 1080px) {
 flex: 0 1 100%;
margin: 0% 0% 5% 0%;
}

}
}
// =====единичный блок=========
.tiny-location {
  width: 100%;
  height: 100%;
  &__body {
    position: relative;
    @include jucb(); 
        @include alis();  
    text-align: left;
    width: 100%;
    height: 100%;
    @media screen and (max-width: 442px) {
  flex: 0 1 100%;
 @include jucc(); 
 } 

  }
  &__icon {
    position: relative;
    z-index: 2;
    text-align: center;
    flex: 0 1 50px;  
 } 
  &__text {
    position: relative;
    z-index: 2;
      @include Text-Style-17;
      flex: 0 1 80%;
      padding: 0% 0% 0% 3%;   
         @media screen and (max-width: 442px) {
 flex: 0 1 100%;
  padding: 3% 0% 0% 0%; 
text-align: center;  
}
  }
}

// ----------------
.content-location-low{
  &__body{
  @include jucb();
    
  }
  &__item{
  flex: 0 1 50%;  
  &:last-child{
    flex: 0 1 50%; 
    @media screen and (max-width: 442px) {
 flex: 0 1 100%;
} 
  }  
@media screen and (max-width: 442px) {
 flex: 0 1 100%;
}
  }
}
 
.tiny-location-low{ 
  &__text{
  @include Text-Style-19;  
  p{
    padding: 0% 5% 5% 0%;    
  }
  }
}


// ---------------------
.location-content-img{ 
  &__body{
 width: 100%;
  padding: 40% 0% 0% 0%;       
  position: relative;
  min-height: 100vh; 
  }
.button-main-white{
  &__body{
 width: auto;
 height: 37px; 
 }
 
  &__link{
    padding: 0px 9px ;  
img{margin-right: 8px;} 
span{@include Text-Style-150;}

}
}

.location-content{
&__buttons{
position: absolute;
width: 100%;
top: 7%; 
left: 0%; 
}
}

.content-buttons{
   &__body{
  @include jucb();
  padding: 0% 11% ; 
  }
}

}
