.wrapper {
  overflow: hidden;
  @include fdc;
  min-height: 100vh;
}

.page {
  position: absolute;
  // max-width: 100%;
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  @include fdc();
  @include anim(0.5s);
  // background: url(../img/background_8.jpg) repeat;
}


.container {
  // outline: 1px solid #4164ff;
  max-width: 88%; 
  margin: 0% 0% 0% 12%;    
}
.container-jil{
  max-width: 91.66666%;
  margin: 0 auto;
   outline: 1px solid #4164ff;

} 
.ibg {
  background-repeat: no-repeat;
  background-size: cover;  
  background-position: center;
  img {
    display: none;
  }
}
.bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

a {
  // @include center(); 
  width: 100%;
  height: 100%;
  text-decoration: none !important;  
  &:hover {
    cursor: pointer;
    z-index: 5;
  }
}

.img-svg {
  width: 30px;
  height: 30px;
}
 .img-bg{
    position: absolute;
    background: #000000;
    opacity: 0.36; 
    width: 100%;
    height: 100%;
    top: 0%;
    left: 0%;
   
  }
.lock {
  overflow: hidden;
}
.icon{
  width: 30px;
  height: 30px;
}
.active{
  opacity: 1;
visibility: visible;
display: flex;  
 }

.content-about{
  background: #FFFFFF;
box-shadow: inset 0px 0px 40px rgba(1, 56, 120, 0.15);     
padding: 6% 0% 6% 0%;
}


//  ------------------------------------
// ==========popup-thanks==============
.popup-thanks {
  background-color: #bb8830;
  border: 2px solid red;
display: none;
  &__body {
        position: fixed;
min-width: 100%;
min-height: 100vh;
top: 0; left: 0;
    background-color: #131f28;
    z-index: 100000;
    opacity: 0.9;
    text-align: center;
    padding: 20px 0px 20px 0px;
   @include center();



  }
  &__header {
flex: 0 1 20%;
@media screen and (max-width: 1000px) {
 flex: 0 1 50%; 
}
@media screen and (max-width: 380px) {
 flex: 0 1 100%;
}
  }

}

// ------ шапка--------------------
.header-popup-thanks {
  &__body {  
@include jucc();
background-color: #e74c45;
position: relative;
}
  &__burger {  
position: absolute;
width: 25px;
height: 25px; 
top: 0; 
right: 0;
background-color: #FF9419;
span{
  width: 20px; 
  height: 2px;
  background-color: #000; 
  position: absolute;
left: 0;
  top: 50% ;
  transform: translate(0%,-50%); 
  @include rotate(45deg);
 
&:last-child{
@include rotate(-45deg);
 
}

 }

}

&__icon {
    background-color: #bee1ff;
    flex: 0 1 100%;
    padding: 10px 0px 10px 0px;
  }

  &__title {
    background-color: #3dbbff;
    flex: 0 1 80%;
    font-family: Lobster-Regular;
    font-size: 25px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
  }

  &__text {
    background-color: #246eb9;
    flex: 0 1 100%;
    font-family: Lobster-Regular;
    font-size: 15px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: left; 
    padding: 30px 20px 50px 20px;
  }
}

